let counter = 0;
function createData(name, calories, fat, carbs, protein) {
    counter += 1;
    return { id: counter, name, calories, fat, carbs, protein };
}

const columnData = [
    { id: 'protein', numeric: false, disablePadding: false, label: 'avatar' },
    { id: 'name', numeric: false, disablePadding: false, label: 'Title' },
    { id: 'fat', numeric: true, disablePadding: false, label: '#' },
    { id: 'calories', numeric: true, disablePadding: false, label: 'Price' },

];

class EnhancedTableHead extends React.Component {
    createSortHandler = property => event => {
        this.props.onRequestSort(event, property);
    };

    render() {
        const { onSelectAllClick, order, orderBy, numSelected, rowCount } = this.props;

        return (
            <TableHead>
                <TableRow>
                    <TableCell padding="checkbox">
                        <Checkbox
                            indeterminate={numSelected > 0 && numSelected < rowCount}
                            checked={numSelected === rowCount}
                            onChange={onSelectAllClick}
                        />
                    </TableCell>
                    {columnData.map(column => {
                        return (
                            <TableCell
                                key={column.id}
                                numeric={column.numeric}
                                padding={column.disablePadding ? 'none' : 'default'}
                                sortDirection={orderBy === column.id ? order : false}
                            >
                                <Tooltip
                                    title="Sort"
                                    placement={column.numeric ? 'bottom-end' : 'bottom-start'}
                                    enterDelay={300}
                                >
                                    <TableSortLabel
                                        active={orderBy === column.id}
                                        direction={order}
                                        onClick={this.createSortHandler(column.id)}
                                    >
                                        {column.label}
                                    </TableSortLabel>
                                </Tooltip>
                            </TableCell>
                        );
                    }, this)}
                </TableRow>
            </TableHead>
        );
    }
}

EnhancedTableHead.propTypes = {
    numSelected: PropTypes.number.isRequired,
    onRequestSort: PropTypes.func.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    order: PropTypes.string.isRequired,
    orderBy: PropTypes.string.isRequired,
    rowCount: PropTypes.number.isRequired,
};

const toolbarStyles = theme => ({
    root: {
        paddingRight: theme.spacing.unit,
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    spacer: {
        flex: '1 1 100%',
    },
    actions: {
        color: theme.palette.text.secondary,
    },
    title: {
        flex: '0 0 auto',
    },
});

let EnhancedTableToolbar = props => {
    const { numSelected, classes } = props;

    return (
        <Toolbar
            className={classNames(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            <div className={classes.title}>
                {numSelected > 0 ? (
                    <Typography color="inherit" variant="subheading">
                        {numSelected} selected
                    </Typography>
                ) : (
                    <Typography variant="title" id="tableTitle">
                        Inside Cart
                    </Typography>
                )}
            </div>
            <div className={classes.spacer} />
            <div className={classes.actions}>
                {numSelected > 0 ? (
                    <Tooltip title="Delete" >
                        <IconButton aria-label="Delete" onClick={()=>props.handleRemoveClick()}>
                            <DeleteIcon />
                        </IconButton>
                    </Tooltip>
                ) : (
                    <Tooltip title="Filter list">
                        <IconButton aria-label="Filter list">
                            <FilterListIcon />
                        </IconButton>
                    </Tooltip>
                )}
            </div>
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    classes: PropTypes.object.isRequired,
    numSelected: PropTypes.number.isRequired,
};

EnhancedTableToolbar = withStyles(toolbarStyles)(EnhancedTableToolbar);

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
    },
    table: {
        minWidth: 1020,
    },
    tableWrapper: {
        overflowX: 'auto',
    },
});

class EnhancedTable extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            order: 'asc',
            orderBy: 'calories',
            selected: [],
            data: [

                createData('Cupcake', 305, 3.7, 67, 4.3),
                createData('Donut', 452, 25.0, 51, 4.9),
                createData('Eclair', 262, 16.0, 24, 6.0),
                createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
                createData('Gingerbread', 356, 16.0, 49, 3.9),
                createData('Honeycomb', 408, 3.2, 87, 6.5),
                createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
                createData('Jelly Bean', 375, 0.0, 94, 0.0),
                createData('KitKat', 518, 26.0, 65, 7.0),
                createData('Lollipop', 392, 0.2, 98, 0.0),
                createData('Marshmallow', 318, 0, 81, 2.0),
                createData('Nougat', 360, 19.0, 9, 37.0),
                createData('Oreo', 437, 18.0, 63, 4.0),

            ].sort((a, b) => (a.calories < b.calories ? -1 : 1)),
            page: 0,
            rowsPerPage: 5,

            orderItems:[],
            volumesWithCount:[],
        };
    }

    componentDidMount() {
        console.log("SHoppingCart fetching db of userId:"+(this.props.location.state && this.props.location.state.referrer));
    }

    componentWillMount() {
        fetch('/api/cart/ofUserId/' + (this.props.location.state && this.props.location.state.referrer))
            .then((response) => response.json())
            .then((responseJson) => {
                this.setState({
                    orderItems: responseJson
                });
                console.log(responseJson);
            })
            .catch((error) => {
                console.error(error);
            });

        fetch('/api/cart/ofUserIdOfVolumeDetail/' + (this.props.location.state && this.props.location.state.referrer))
            .then((response) => response.json())
            .then((responseJson) => {
                this.setState({
                    volumesWithCount: responseJson
                });
                console.log(responseJson);
            })
            .catch((error) => {
                console.error(error);
            });

        this.state.volumesWithCount.map(book=>{
            let volumeCount=this.getVolumeCount(book.id);
            console.log("FOUND count:"+volumeCount);
        });



    }

    handleRequestSort = (event, property) => {
        const orderBy = property;
        let order = 'desc';

        if (this.state.orderBy === property && this.state.order === 'desc') {
            order = 'asc';
        }

        const data =
            order === 'desc'
                ? this.state.data.sort((a, b) => (b[orderBy] < a[orderBy] ? -1 : 1))
                : this.state.data.sort((a, b) => (a[orderBy] < b[orderBy] ? -1 : 1));

        this.setState({ data, order, orderBy });
    };

    handleSelectAllClick = (event, checked) => {
        if (checked) {
            this.setState({ selected: this.state.data.map(n => n.id) });
            return;
        }
        this.setState({ selected: [] });
    };

    handleRemoveClick=()=>{
        console.log(this.state.selected);
        console.log("remove items from order db.");

        const obj = this.state.selected;
        const method = "DELETE";
        const body = JSON.stringify(obj);
        const headers = {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        };
        fetch("/api/cart/removeItemsOfUserId/"+(this.props.location.state && this.props.location.state.referrer),
            {method, headers, body}).then((res)=> res.json()).then(console.log).catch(console.error);

        //
        //after remove, fetch again to remove removed item
        //
        this.setState({orderItems:[],volumesWithCount:[],isLoading:true});
        fetch('/api/cart/ofUserId/' + (this.props.location.state && this.props.location.state.referrer))
            .then((response) => response.json())
            .then((responseJson) => {
                this.setState({
                    orderItems: responseJson
                });
                console.log(responseJson);
            })
            .catch((error) => {
                console.error(error);
            });

        fetch('/api/cart/ofUserIdOfVolumeDetail/' + (this.props.location.state && this.props.location.state.referrer))
            .then((response) => response.json())
            .then((responseJson) => {
                this.setState({
                    volumesWithCount: responseJson
                });
                console.log(responseJson);
            })
            .catch((error) => {
                console.error(error);
            });
        this.setState({isLoading:false});
        //
        //
        //

    };

    shouldComponentUpdate(nextProps,nextState){
        return nextState.orderItems.length!==this.state.orderItems.length;
    }

    handleClick = (event, id) => {

        const { selected } = this.state;
        const selectedIndex = selected.indexOf(id);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, id);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1),
            );
        }

        this.setState({ selected: newSelected });

    };

    handleChangePage = (event, page) => {
        this.setState({ page });
    };

    handleChangeRowsPerPage = event => {
        this.setState({ rowsPerPage: event.target.value });
    };



    getVolumeCount(volumeId){
        let foundItem=this.state.orderItems.find(item=>{
            if(item.bookId===volumeId) {
                return item;
            }
        });
        return foundItem.count;
    };



    isSelected = id => this.state.selected.indexOf(id) !== -1;


    render() {
        const { classes } = this.props;
        const { data, order, orderBy, selected, rowsPerPage, page ,volumesWithCount} = this.state;
        const emptyRows = rowsPerPage - Math.min(rowsPerPage, data.length - page * rowsPerPage);

        if(this.state.isLoading)
            return(
                <div>
                <CircularProgress size={60} thickness={7} />
                </div>
            );

        return (
            <Paper className={classes.root}>
                <EnhancedTableToolbar numSelected={selected.length} handleRemoveClick={this.handleRemoveClick} />
                <div className={classes.tableWrapper}>
                    <Table className={classes.table} aria-labelledby="tableTitle">
                        <EnhancedTableHead
                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={this.handleSelectAllClick}
                            onRequestSort={this.handleRequestSort}
                            rowCount={data.length}
                        />
                        <TableBody>
                            {volumesWithCount.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(n => {

                                let volumeCount=this.getVolumeCount(n.id);
                                //console.log("volumeCount:"+volumeCount);


                                const isSelected = this.isSelected(n.id);
                                return (
                                    <TableRow
                                        hover
                                        onClick={event => this.handleClick(event, n.id)}
                                        role="checkbox"
                                        aria-checked={isSelected}
                                        tabIndex={-1}
                                        key={n.id}
                                        selected={isSelected}
                                    >
                                        <TableCell padding="checkbox">
                                            <Checkbox checked={isSelected} />
                                        </TableCell>
                                        <TableCell>
                                            <img src={n.imageLinks}
                                                 width="60"
                                                 height="100"
                                                 alt={n.title}>

                                            </img>
                                        </TableCell>
                                        <TableCell component="th" scope="row" padding="none">
                                            <span>
                                            {n.title}
                                            <Button variant="fab" mini color="default" aria-label="info_icon" component={Link}  to={{
                                                pathname: "/detail/"+n.id,
                                                state: { referrer: (this.props.location.state && this.props.location.state.referrer) }
                                            }} >
                                                <InfoIcon />
                                            </Button>
                                            </span>
                                        </TableCell>
                                        <TableCell numeric>{volumeCount}</TableCell>
                                        <TableCell numeric>¥{22}</TableCell>

                                    </TableRow>
                                );
                            })}
                            {emptyRows > 0 && (
                                <TableRow style={{ height: 49 * emptyRows }}>
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </div>
                <TablePagination
                    component="div"
                    count={data.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    backIconButtonProps={{
                        'aria-label': 'Previous Page',
                    }}
                    nextIconButtonProps={{
                        'aria-label': 'Next Page',
                    }}
                    onChangePage={this.handleChangePage}
                    onChangeRowsPerPage={this.handleChangeRowsPerPage}
                />
                <Button variant="raised" size="large" color="primary" className={classes.button}>
                    Buy
                </Button>
            </Paper>
        );
    }
}

EnhancedTable.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(EnhancedTable);




